(function (gmapi) {
    if (typeof google === 'undefined') {
        gmapi.init = function () {};
        gmapi.draw = function () {};
        return false;
    }

    var geocoder;
    var map;
    var infowindow = new google.maps.InfoWindow();
    var marker;
    var lat, lng, infoWindowContent;
    var mapAreaMaxWidth;

    function setupArea () {
        mapAreaMaxWidth = $('#map-canvas').parent().width();
        //$('#map-canvas').css('max-width', '600px');
        //$('#map-canvas').css('max-height', '400px');
        $('#map-canvas').css('width', '100%');
        $('#map-canvas').css('height', '400px');
    }

    function initialize (lt, lg, iwc) {
        lat = lt;
        lng = lg;
        infoWindowContent = iwc;
        setupArea();
        geocoder = new google.maps.Geocoder();
        var latlng = new google.maps.LatLng(lat, lng);
        var mapOptions = {
            zoom: 16,
            center: latlng//,
            //mapTypeId: 'roadmap'
        }
        map = new google.maps.Map(document.getElementById('map-canvas'), mapOptions);
        //map.panBy(0, 200);
        google.maps.event.addListenerOnce(map, 'idle', function () {
            infowindow.open(map, marker);
        });
    }

    function codeLatLng () {
        // var lat = 40.730885;
        // var lng = -73.997383;
        var latlng = new google.maps.LatLng(lat, lng);
        geocoder.geocode({'latLng': latlng}, function(results, status) {
            if (status == google.maps.GeocoderStatus.OK) {
                if (results[1]) {
                    //map.setZoom(11);
                    marker = new google.maps.Marker({
                        position: latlng,
                        map: map
                    });
                    //infowindow.setContent(results[1].formatted_address);
                    infowindow.setOptions({ maxWidth: mapAreaMaxWidth / 2 });
                    infowindow.setContent(infoWindowContent);
                    //infowindow.open(map, marker);
                } else {
                    console.log('No results found');
                }
            } else {
                console.log('Geocoder failed due to: ' + status);
            }
        });
    }
    //google.maps.event.addDomListener(window, 'load', initialize);
    gmapi.init = initialize;
    gmapi.draw = codeLatLng;
} (window.Diskupon.gmapsapi = window.Diskupon.gmapsapi || {}));
