(function (diskupon) {

    diskupon.App.module("Deals.Details", function (DealsDetails, App, Backbone, Marionette, $, _) {

        this.startWithParent = false;

        DealsDetails.on('start', function () {

        });

        DealsDetails.DealsDetailsView = Marionette.ItemView.extend({
            template: tpl_deal_details,
            tagName: 'div',
            className: 'dsk-dealDetails',
            onRender: function () {
                var that = this;
                this.$el.find('#deal_images_navs').flexslider({
                    animation: "slide",
                    controlNav: false,
                    animationLoop: false,
                    slideshow: false,
                    itemWidth: 100,
                    asNavFor: '#deal_images'
                });
                this.$el.find('#deal_images').flexslider({
                    animation: "slide",
                    controlNav: false,
                    animationLoop: false,
                    slideshow: false,
                    sync: "#deal_images_navs"
                });
                this.$el.find('.sliderMain-imgContainer').imgLiquid();
                this.$el.find('#map-canvas').show('fast', function () {
                    // maps of deals location
                    var pkpn = that.model.get('PrimaryKupon');
                    window.hehe = pkpn;
                    if (pkpn.KuponLocation) {
                        var ct = '<h4>' + pkpn.KuponLocation.LocationName + '</h4> \
                                  <p>' + pkpn.KuponLocation.LocationAddress + '</p> \
                                 ';
                        //this.$el.find('#dsk_dd_tabs a[data-toggle="tab"]').on('shown.bs.tab', function (e) {
                        //this.$el.find('#dsk_dd_tabs4 a[data-toggle="tab"]').on('shown.bs.tab', function (e) {
                            //debugger;
                            //if ($(e.target).hasClass('dskf-vendorInfo')) {
                                window.Diskupon.gmapsapi.init(pkpn.KuponLocation.Latitude, pkpn.KuponLocation.Longitude, ct);
                                window.Diskupon.gmapsapi.draw();
                            //}
                        //});
                    }
                });
                // this.$el.find('#dsk_dd_tabs a:first').tab('show');
            }
        });

        DealsDetails.Controller = Marionette.Controller.extend({
            initialize: function (options) {
                _.bindAll(this, 'showDealsDetails', 'renderDealsDetails');
                this.DealsDetailsRegion = options.DealsDetailsRegion;
                this.DealsDetailsMobileRegion = options.DealsDetailsMobileRegion;
                this.DealId = 0;
            },

            renderDealsDetails: function (deal) {
                var view = new DealsDetails.DealsDetailsView({
                    model: deal
                });
                this.DealsDetailsRegion.show(view);

                this.trigger('dealdetails:detailsshown', deal.toJSON());

                // TODO: details for mobile region
            },

            showDealsDetails: function () {
                var that = this;
                var repo = new App.Deals.Repository();

                var promise = repo.getOne(this.DealId);

                promise.done(function (deal) {
                    that.renderDealsDetails(deal);
                });
            }
        });

        DealsDetails.addInitializer(function (args) {
            DealsDetails.controller = new DealsDetails.Controller({
                DealsDetailsRegion: args.DealsDetailsRegion,
                DealsDetailsMobileRegion: args.DealsDetailsMobileRegion
            });

            DealsDetails.controller.listenTo(DealsDetails.controller, 'dealdetails:detailsshown', function (dealModel) {
                //console.log(dealModel.DealCategoryId);
                App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.ByCategory, dealModel.DealCategoryId);
            });
        });

        DealsDetails.addFinalizer(function () {
            //console.log('DealsDetails.addFinalizer');
            DealsDetails.controller.close();
            delete DealsDetails.controller;
        });

    });
} (window.Diskupon));