/*** backbone force use POST when fetching ***/
Backbone.ajax = function () {
    var args = Array.prototype.slice.call(arguments);
    if (args[0]) {
        args[0].type = 'POST';
    }
    return Backbone.$.ajax.apply(Backbone.$, args);
};

