(function (diskupon) {
    diskupon.App.module("Deals", function (Deals, App, Backbone, Marionette, $, _) {

        var dealListPageLayout = Backbone.Marionette.Layout.extend({
            template: "#dealListPageLayout_template",
            regions: {
                HomeSlides: '#dsk_homeSlides',
                MainContent: '#dsk_content',
                MainContentMobile: '#dsk_content_mobile'
            },
            className: 'container'
        });

        var dealDetailsPageLayout = Backbone.Marionette.Layout.extend({
            template: "#dealDetailsPageLayout_template",
            regions: {
                MainContent: '#dsk_content',
                MainContentMobile: '#dsk_content_mobile'
            },
            className: 'container'
        });

        var submodules = ['Deals.List', 'Deals.Details'];

        Deals.on('start', function () {
            Deals.listLayout = new dealListPageLayout();
            Deals.detailsLayout = new dealDetailsPageLayout();
        });

        Deals.Controller = Marionette.Controller.extend({
            initialize: function (options) {
                _.bindAll(this, 'showDealsList', 'showDealDetails');
            },

            _renderListLayout: function () {
                Deals.listLayout.render();
                App.regionManager.get('Main').show(Deals.listLayout);
            },

            _renderDetailsLayout: function () {
                Deals.detailsLayout.render();
                App.regionManager.get('Main').show(Deals.detailsLayout);
            },

            _startListSubmodule: function () {
                var options = {
                    DealsListRegion: Deals.listLayout.MainContent,
                    DealsListMobileRegion: Deals.listLayout.MainContentMobile
                };
                App.vent.trigger('submodule:start', submodules, 'Deals.List', options);
            },

            _startDetailsModule: function () {
                var options = {
                    DealsDetailsRegion: Deals.detailsLayout.MainContent,
                    DealsDetailsMobileRegion: Deals.detailsLayout.MainContentMobile
                };
                App.vent.trigger('submodule:start', submodules, 'Deals.Details', options);
            },

            _startHomeSlidesModule: function () {
                var options = {
                    HomeSlidesRegion: Deals.listLayout.HomeSlides
                };
                App.module("HomeSlides").start(options);
            },

            showDealsList: function (options) {
                var defaultOptions = {
                    categoryId: diskupon.DealCategory.All,
                    searchQuery: ''
                };
                options = $.extend({}, defaultOptions, options);

                this._renderListLayout();
                this._startHomeSlidesModule();
                this._startListSubmodule();

                App.Deals.List.controller.SearchParams.CategoryId = options.categoryId;
                App.Deals.List.controller.SearchParams.SearchQuery = options.searchQuery;
                App.Deals.List.controller.showDeals();

                App.HomeSlides.controller.show();
            },

            showDealDetails: function (dealId) {
                this._renderDetailsLayout();
                this._startDetailsModule();

                App.Deals.Details.controller.DealId = dealId;
                App.Deals.Details.controller.showDealsDetails();
            },

            showTopDealsGroupByCategory: function () {
                this._renderListLayout();
                this._startHomeSlidesModule();

                App.HomeSlides.controller.show();
            }
        });

        Deals.addInitializer(function () {
            Deals.controller = new Deals.Controller({
            });
        });

        Deals.addFinalizer(function () {
            Deals.controller.close();
            delete Deals.controller;
        });

    });
} (window.Diskupon));