(function (diskupon) {
    diskupon.App.module("Cart.CartLink", function (CartLink, App, Backbone, Marionette, $, _) {

        CartLink.CartLinkView = Marionette.ItemView.extend({
            template: tpl_cart_link,
            events: {
                'click #dsk_showCartSummary': 'showCartSummary'
            },
            initialize: function () {
                _.bindAll(this, 'showCartSummary');
            },
            onRender: function () {
                var dis = this;
                var a = this.$el.find('#cartSummaryModal');
                a.modal({
                    show: false
                });
                a.on('show.bs.modal', function () {
                    dis.$el.find('#cartSummaryModal .dsk-cartList').empty();
                    dis.$el.find('#cartSummaryModal .loader').show();
                });
                a.on('shown.bs.modal', function () {
                    var repo = new App.Cart.Repository();
                    var promise = repo.getCartSummary();
                    promise.done(function (data) {
                        /// data is a collection, so get the models property
                        var models = data.models;

                        dis.$el.find('#cartSummaryModal .error-cart').hide();
                        dis.$el.find('#cartSummaryModal .empty-cart').hide();
                        if (models.length === 0) {
                            dis.$el.find('#cartSummaryModal .empty-cart').show();
                        }

                        /// populate cart summary list
                        var rt = dis.$el.find('#cartSummaryModal .dsk-cartList');
                        for (var i = 0; i < models.length; i++) {
                            var d = models[i];
                            var dj = d.toJSON();
                            var re = tpl_cart_summary_item(dj);
                            rt.append(re);
                        }
                    });
                    promise.fail(function () {
                        dis.$el.find('#cartSummaryModal .error-cart').show();
                    });
                    promise.always(function () {
                        dis.$el.find('#cartSummaryModal .loader').hide();
                    });
                });
            },
            showCartSummary: function (e) {
                e.preventDefault();
                var a = this.$el.find('#cartSummaryModal');
                a.modal('show');
            }
        });

        CartLink.Controller = Marionette.Controller.extend({
            initialize: function (options) {
                _.bindAll(this, 'show');
                this.ContainerRegion = options.ContainerRegion;
            },

            show: function () {
                var dis = this;
                var view = new CartLink.CartLinkView({
                    el: $("#dsk_cartLinkSection")
                });
                this.listenTo(view, 'cartlink:click', function () {
                    dis.trigger('cartsummary:demandshow');
                });
                this.ContainerRegion.attachView(view);
                this.ContainerRegion.show(view);
            }
        });

        CartLink.addInitializer(function () {
            console.log('CartLink:Initializer');
            CartLink.controller = new CartLink.Controller({
                ContainerRegion: App.regionManager.get('Header')
            });
        });

    });
} (window.Diskupon));