(function (diskupon, App) {

    var appRouterController = function () {
        function parseQueryString (queryString) {
            var qsObj = {};
            if (queryString) {
                var decoded = decode(queryString);
                var arrQs = decoded.split('&');
                for (var i = 0; i < arrQs.length; i++) {
                    var kp = arrQs[i].split('=');
                    qsObj[kp[0]] = qsObj[kp[1]];
                }
            }
            return qsObj;
        }

        return {
            showHeader: function (options) {
                App.Cart.controller.showCartLink();
                App.RegionSelector.controller.show();
                App.TopMenu.controller.show();
            },

            showAffiliates: function () {
                App.Affiliates.controller.show();
            },

            showHomePage: function () {
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showTopDealsGroupByCategory();
            },

            showAllKuponsPage: function () {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.All);
                });
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealsList({
                    categoryId: diskupon.DealCategory.All,
                    searchQuery: ''
                });
            },

            showNewKuponsPage: function () {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.New);
                });
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealsList({
                    categoryId: diskupon.DealCategory.New,
                    searchQuery: ''
                });
            },

            showEndingSoonKuponsPage: function () {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.EndingSoon);
                });
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealsList(diskupon.DealCategory.EndingSoon);
            },

            showHotKuponsPage: function () {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.Hot);
                });
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealsList({
                    categoryId: diskupon.DealCategory.Hot,
                    searchQuery: ''
                });
            },

            showKuponsByCategory: function (id, name) {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.ByCategory, id);
                });
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealsList({
                    categoryId: id,
                    searchQuery: ''
                });
            },

            showHoneymoon: function () {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.Honeymoon);
                });
                this.showHeader();
                this.showAffiliates();
                // TODO:!!!
            },

            showKuponDetails: function (id, name) {
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealDetails(id);
            },

            searchForKupons: function (query) {
                App.TopMenu.controller.listenTo(App.TopMenu.controller, 'topmenu:shown', function () {
                    App.TopMenu.controller.trigger('topmenu:changeactivemenuitem', diskupon.DealCategory.All);
                });
                this.showHeader();
                this.showAffiliates();
                App.Deals.controller.showDealsList({
                    categoryId: diskupon.DealCategory.All,
                    searchQuery: query
                });
            }
        };
    } ();

    diskupon.AppRouter = Backbone.Marionette.AppRouter.extend({
        execute: function(callback, args) {
            //console.log(args);
        }
    });

    App.addInitializer(function () {
        new diskupon.AppRouter({
            controller: appRouterController,
            appRoutes: {
                "": "showHomePage",
                "kupons/all": "showAllKuponsPage",
                "kupons/new": "showNewKuponsPage",
                "kupons/endingsoon": "showEndingSoonKuponsPage",
                "kupons/hot": "showHotKuponsPage",
                "kupons/category/:id/:name": "showKuponsByCategory",
                "kupons/honeymoon": "showHoneymoon",
                "kupons/details/:id/:name": "showKuponDetails",
                "kupons/search/:query": "searchForKupons"
            }
        });
    });

} (window.Diskupon, window.Diskupon.App));