(function (diskupon) {
    diskupon.App.module("Cart", function (Cart, App, Backbone, Marionette, $, _) {

        var CartItemSummaryModel = Backbone.Model.extend({
            //url: 'api/v1/cart',
            // getCartSummary: function (params) {
            //     this.fetch({ data: $.param(params) });
            // }
        });

        var CartItemSummaryCollection = Backbone.Collection.extend({
            model: CartItemSummaryModel,
            url: '/api/v1/cart'
        });

        Cart.Repository = Marionette.Controller.extend({
            initialize: function () {
                //_.bindAll(this, 'search');
            },
            getCartSummary: function () {
                // param later on
                var deferred = $.Deferred();
                var cisColl = new CartItemSummaryCollection();

                cisColl.on('reset', function (cis) {
                    //console.log(cis);
                    deferred.resolve(cis);
                });

                cisColl.fetch({ reset: true });

                return deferred.promise();
            }
        });

    });
} (window.Diskupon));